body {
  font-family: "Open Sans", sans-serif;
  font-size: 1.6rem;
  line-height: 1.2;
  background: #f3f3f3;
}

p {
  color: #000000;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Open Sans", sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.contenido-centrado {
  max-width: 800px;
}

.alinear-derecha {
  display: flex;
  justify-content: flex-end;
}

.card__txt {
  text-align: center;
  margin: 0;
}

.lds-facebook {
  display: inline-block;
  position: absolute;
  width: 10rem;
  height: 10rem;
}

.lds-facebook div {
  display: inline-block;
  position: relative;
  left: 8px;
  width: 16px;
  background: #CF6766;
  animation: lds-facebook 1.2s cubic-bezier(0, 0.5, 0.5, 1) infinite;
}

.lds-facebook div:nth-child(1) {
  left: 8px;
  animation-delay: -0.24s;
}

.lds-facebook div:nth-child(2) {
  left: 32px;
  animation-delay: -0.12s;
}

.lds-facebook div:nth-child(3) {
  left: 56px;
  animation-delay: 0;
}

@keyframes lds-facebook {
  0% {
    top: 8px;
    height: 64px;
  }
  50%, 100% {
    top: 24px;
    height: 32px;
  }
}
.loader {
  background-color: #30415D;
  width: 100%;
  height: 100vh;
  z-index: 10000;
  display: flex;
  align-items: center;
  justify-content: center;
  right: 0;
  bottom: 0;
  top: 0;
  left: 0;
  position: fixed;
  clip-path: circle(100% at 50% 50%);
  transition: clip-path 1.8s ease-in-out;
}

.loader2 {
  clip-path: circle(0% at 100% 100%);
}

body.dark-mode {
  background-color: #292929;
}
body.dark-mode .hero {
  background-image: linear-gradient(rgba(63, 63, 63, 0.4), rgba(63, 63, 63, 0.4)), url(../img/fondo.webp);
}
body.dark-mode .hero__perfil-img {
  border: 4px solid #292929;
}
body.dark-mode footer {
  background-color: #4e4e4e;
}
body.dark-mode .perfil h2, body.dark-mode .perfil p {
  color: #FFFFFF;
}
body.dark-mode .section-inner .section-experience {
  background-color: #4e4e4e;
}
body.dark-mode .section-inner .section-education {
  background-color: #4e4e4e;
}
body.dark-mode .section-inner .section-skils {
  background-color: #4e4e4e;
}
body.dark-mode .section-inner h2, body.dark-mode .section-inner h3, body.dark-mode .section-inner h4, body.dark-mode .section-inner p {
  color: #FFFFFF;
}
body.dark-mode .nav-gallery {
  background-color: #4e4e4e;
}
body.dark-mode .btn-mode--dark-mode {
  background-color: #CF6766;
}
body.dark-mode .btn-mode--dark-mode::after {
  left: 0;
}

.hero {
  height: 100vh;
  background-image: linear-gradient(rgba(88, 24, 46, 0.4), rgba(88, 24, 46, 0.4)), url(../img/fondo.webp);
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
  text-align: center;
  color: #FFFFFF;
  transition: all 5s ease-in-out;
}
.hero .hero__encabezado {
  font-size: 3.5rem;
}
.hero .hero__keywords {
  margin: 0;
  padding: 1rem;
  font-size: 2.5rem;
}
.hero .hero__perfil-img {
  width: 13rem;
  height: 13rem;
  border-radius: 10rem;
  overflow: hidden;
  border: 4px solid #CF6766;
  transition: all 0.5s ease;
}

.h-100 {
  height: 100%;
  display: flex;
  flex-direction: column;
}

.contacto {
  display: flex;
  gap: 2rem;
  justify-content: space-between;
  align-items: flex-start;
  padding-top: 3rem;
  height: 8rem;
}
.contacto .redes {
  display: none;
}
@media (min-width: 768px) {
  .contacto .redes {
    display: flex;
    gap: 2rem;
  }
}
.contacto .redes .tw {
  background: #1da1f2;
}
.contacto .redes .fb {
  background: #3b5998;
}
.contacto .redes .ig {
  background: #c13584;
}
.contacto .redes .email {
  background-color: #FF2525;
}
.contacto .redes i {
  width: 4rem;
  height: 4rem;
  font-size: 3rem;
  color: #FFFFFF;
  border-radius: 10rem;
  display: flex;
  align-items: center;
  justify-content: center;
}
.contacto .contenedor-icono:hover {
  animation: icono 0.5s ease;
}
@keyframes icono {
  from {
    transform: rotate3d(0, 0, 0, 0);
  }
  to {
    transform: rotate3d(0, 1, 0, 360deg);
  }
}
.contacto .boton-img {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  gap: 3rem;
}
.contacto .btn-mode {
  display: none;
  padding: 0.8rem;
  border: none;
  border-radius: 2rem;
  outline: none;
  margin-bottom: 1.8rem;
  position: relative;
  background-color: #031424;
  color: #FFFFFF;
  height: 3rem;
  width: 8rem;
  display: flex;
  justify-content: space-between;
  align-content: center;
  align-items: flex-start;
  cursor: pointer;
  transition: all 0.5s ease;
}
.contacto .btn-mode i {
  font-size: 1.5rem;
}
.contacto .btn-mode::after {
  content: "";
  top: 0;
  width: 50%;
  height: 100%;
  right: 0;
  background-color: #FFFFFF;
  position: absolute;
  border-radius: 2rem;
  transition: left 0.3s ease-in-out;
  box-shadow: 0px 0px 2px 2px rgba(0, 0, 0, 0.2);
}

.nav {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-basis: 100%;
}

.main-nav {
  display: flex;
  justify-content: center;
  gap: 1rem;
}
.main-nav .main-nav__enlace {
  color: #FFFFFF;
  border-radius: 1rem;
  padding: 0.3rem 3rem;
  background-color: rgba(0, 0, 0, 0.3);
  transition: all 0.2s ease;
}
.main-nav .main-nav__enlace:hover {
  color: #CF6766;
}

.btn-primary {
  background-color: transparent;
  border: none;
  padding: 0;
}

.btn {
  height: 4rem;
  width: 4rem;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0;
}
.btn i {
  font-size: 3rem;
}
@media (min-width: 768px) {
  .btn {
    display: none;
  }
}

.offcanvas {
  background-color: #8EAEBD;
}

footer {
  background-color: #031424;
  height: 30rem;
}
footer .main-footer {
  padding: 2rem 0;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
}
footer .copy p {
  font-size: 1.5rem;
  text-align: center;
  color: #FFFFFF;
}

.direccion {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin: 0 auto;
}

.direccion__iconos {
  display: flex;
  flex-direction: column;
  gap: 2rem;
  align-items: center;
}

.direction__iconos-i {
  text-align: center;
}
.direction__iconos-i i {
  font-size: 3rem;
}

.footer-content {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  text-align: center;
}

.footer-content h3 {
  font-size: 1.8rem;
  font-weight: 400;
  text-transform: capitalize;
  margin: 0;
  margin-top: 3rem;
  color: #FFFFFF;
}
.footer-content h3::after {
  content: "";
  border-bottom: 1px solid #031424;
  margin-top: 1rem;
  display: block;
  width: 18rem;
  height: 0.5rem;
  background-color: #4158D0;
  background: linear-gradient(43deg, #4158D0 0%, #C850C0 46%, #FFCC70 100%);
}

.footer-content p {
  max-width: 500px;
  margin: 10px auto;
  line-height: 28px;
  font-size: 14px;
  color: #FFFFFF;
}

.socials {
  list-style: none;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 1rem 0 3rem 0;
}
.socials li {
  margin: 0 1rem;
}
.socials a {
  color: #FFFFFF;
}
.socials a:hover {
  color: #CF6766;
}
.socials a i {
  font-size: 1.1rem;
  transition: color 0.4s ease;
}

.footer-bottom {
  background: #000000;
  width: 100vw;
  padding: 2rem 0;
  text-align: center;
}
.footer-bottom p {
  font-size: 1.4rem;
  text-transform: capitalize;
}
.footer-bottom span {
  text-transform: uppercase;
  opacity: 0.4;
  font-weight: 200;
}

.nav-gallery {
  text-align: center;
  padding-bottom: 3rem;
  background-color: #8EAEBD;
  padding: 2rem;
  border-radius: 1rem;
  -webkit-box-shadow: 3px 3px 5px 0px rgba(0, 0, 0, 0.51);
  -moz-box-shadow: 3px 3px 5px 0px rgba(0, 0, 0, 0.51);
  box-shadow: 3px 3px 5px 0px rgba(0, 0, 0, 0.51);
  margin: 1.5rem 0;
  transition: all 0.5s ease;
}
@media (min-width: 768px) {
  .nav-gallery {
    padding-bottom: 2rem;
  }
}

.portafolio h2 {
  font-weight: 400;
}

.nav-gallery__enlace {
  color: #FFFFFF;
  padding: 1rem;
  border-bottom: 3px #ff85c200 solid;
  transition: all ease-in-out 0.3s;
  margin-bottom: 1rem;
}
.nav-gallery__enlace:hover {
  border-bottom: 3px #FFFFFF solid;
}

.nav-gallery__enlace--active {
  border-bottom: 3px #FFFFFF solid;
}

.nav-gallery__enlace--w100 {
  width: 100%;
  display: block;
  border: none;
}
@media (min-width: 768px) {
  .nav-gallery__enlace--w100 {
    width: 100%;
    display: inline;
  }
}

.grid {
  position: relative;
  opacity: 0;
  transition: opacity 0.8s linear 1s;
  width: 100%;
}

.images-loaded .grid {
  opacity: 1;
}

.item {
  display: block;
  position: absolute;
  margin: 1rem;
  padding: 0;
}
@media (min-width: 768px) {
  .item {
    width: calc(33.33% - 20px);
  }
}

.item.muuri-item-hidden {
  z-index: 0;
}

.item.muuri-item-releasing {
  z-index: 2;
}

.item.muuri-item-dragging {
  z-index: 3;
}

.item-content {
  position: relative;
  cursor: pointer;
}

.item-content > img {
  display: block;
  border-radius: 6px;
}

.images-loaded .loading {
  opacity: 0;
}

.overlay {
  position: fixed;
  background-color: rgba(0, 0, 0, 0.5);
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  display: none;
  place-items: center;
  z-index: 1;
}
.overlay .overlay__parrafo {
  background-color: #FFFFFF;
  border-radius: 1rem;
}
.overlay .overlay__parrafo p {
  text-align: center;
  margin: 0;
  padding: 1rem;
}

.overlay-active {
  display: grid;
}

.overlay__contenedor {
  display: grid;
  place-items: center;
  width: 80%;
}
@media (min-width: 768px) {
  .overlay__contenedor {
    width: 30%;
  }
}
.overlay__contenedor #overlay__contenedor--boton {
  background-color: #FFFFFF;
  display: grid;
  place-items: center;
  font-size: 3rem;
  font-weight: bold;
  border-radius: 50%;
}
.overlay__contenedor #overlay__contenedor--boton:hover {
  cursor: pointer;
}

.perfil, .cv, .portafolio {
  display: none;
  opacity: 0;
}

.display {
  display: block;
  opacity: 1;
  transition: all ease-in-out 2s;
}

.container {
  width: 100%;
  padding: 2rem;
}

.form {
  padding: 1rem;
  width: 100%;
  max-width: 80rem;
  background-color: #30415D;
  border-radius: 1rem;
}
.form fieldset {
  border: none;
}
.form fieldset .title {
  color: #FFFFFF;
}
.form .contact-form {
  background-color: #031424;
}

.contact-info p {
  color: #FFFFFF;
}
.contact-info .info p {
  color: #FFFFFF;
}

.input-container {
  margin-top: 2.5rem;
  position: relative;
}

.input {
  width: 100%;
  outline: none;
  border: 0.2rem solid #FFFFFF;
  background: none;
  padding: 1rem;
  color: #FFFFFF;
  font-size: 2.5rem;
  border-radius: 2rem;
  transition: 0.3s;
}

textarea.input {
  padding: 1rem;
  min-height: 20rem;
  border-radius: 2rem;
}

.input-container label {
  position: absolute;
  top: 50%;
  left: 1rem;
  transform: translateY(-50%);
  padding: 1rem;
  color: #FFFFFF;
  font-size: 2rem;
  pointer-events: none;
  transition: 0.5s;
}

.input-container.textarea label {
  top: 1rem;
  transform: translateY(0);
}

.boton {
  padding: 1rem;
  background-color: #FFFFFF;
  border: 0.2rem solid #FFFFFF;
  font-size: 2rem;
  line-height: 1;
  border-radius: 2rem;
  outline: none;
  cursor: pointer;
  transition: 0.3s;
  margin-top: 2rem;
}
.boton:hover {
  background-color: transparent;
  color: #FFFFFF;
}

.input-container.focus label {
  top: 0;
  transform: translateY(-80%);
  font-size: 2rem;
}

.contact-info {
  padding: 2rem;
}
.contact-info .title {
  color: #FFFFFF;
}

.information {
  display: flex;
  align-items: center;
  font-size: 1rem;
  gap: 2rem;
}
.information i {
  font-size: 3rem;
  color: #CF6766;
}

.social-media {
  padding: 1rem;
}

@media (min-width: 768px) {
  .form {
    display: grid;
    gap: 0;
    grid-template-columns: repeat(2, 1fr);
  }
}
.alerta {
  background-color: #b90000;
  color: #FFFFFF;
  text-transform: uppercase;
  text-align: center;
  border-radius: 1rem;
  padding: 1rem;
  max-width: 80%;
  margin: 2rem auto;
}

.exito {
  background-color: #00b900;
  color: #FFFFFF;
  text-transform: uppercase;
  text-align: center;
  border-radius: 1rem;
  padding: 1rem;
  max-width: 80%;
  margin: 2rem auto;
}

.box {
  right: 3rem;
  bottom: 3rem;
  position: absolute;
  transform: translate(-50%, -50%);
}
@media (min-width: 768px) {
  .box {
    right: 8rem;
    bottom: 8rem;
  }
}
.box span {
  display: block;
  width: 2rem;
  height: 2rem;
  border-bottom: 2px solid #FFFFFF;
  border-right: 2px solid #FFFFFF;
  transform: rotate(45deg);
  margin: -1rem;
  animation: animate 2s infinite;
}
.box span:nth-child(2) {
  animation-delay: -0.2s;
}
.box span:nth-child(3) {
  animation-delay: -0.4s;
}

@keyframes animate {
  0% {
    opacity: 0;
    transform: rotate(45deg) translate(-2rem, -2rem);
  }
  50% {
    opacity: 1;
  }
  100% {
    opacity: 0;
    transform: rotate(45deg) translate(2rem, 2rem);
  }
}
